import React from 'react'
import '../../App.css'

export default function Experience() {

    return (
        <>
        <h1 className='Experience'>Experience</h1>
        <div className='Experience-body'>
        <div id="exp-content">
                    <h2 id="exp-h">Academic Experience</h2> 
                    <br/>
                    <p id="exp-p">
                        &nbsp;&nbsp;&nbsp;During my time in school, I've worked with many team for academic projects. 
                        I have experience of working with people who have different background and knowledge.
                        It helps me to develope a good interpersonel skill to quickly adapt to new groups.
                        Since I studied computer engineering and computer science, I've worked in multiple software projects. 
                        The majority of those were team projects. 
                        I like front-end development because it directly affects users' experience.
                        However, I have taken databases and computer security courses.
                        I can work with both front-end and back-end development. 
                        In school we were taught some basic programing languages such as Java, C, SQL, and HTML.
                        These tools are useful but mostly outdated. 
                        In my free time, I've learned other programing languages, and one of them is JavaScript.
                        It is a very powerful language that leads me to learn using some of its most useful applications such as NodeJS and ReactJS. 
                        <br/>                
                        <br/>  
                        <h2 id="exp-h">Work Experience</h2> 
                        <br/>
                        &nbsp;&nbsp;&nbsp;I started at FG Construction, LLC just a couple of months after I graduated.
                        It was the first opportunity that I got since I graduated during a pandamic. I worked there for 9 months.
                        I started as a project engineer.
                        My responsibility mostly review construction plans to help estimators to bid for up coming jobs.
                        Since I have computer related background, I was selected to train to become an estimator with a new estimating software that they recently purchased.
                        After 2 months since I started, I became a jr. estimator. 
                        We won some big jobs that would keep our workers busy for a while, so we slowed down in bidding for more jobs.
                        I was then trained to become a project manager since I've learned the process of construction from estimating its cost.
                        In the next 6 months there, I was a project manager assistant. My responsibilies include negotiating with material suppliers and subcontractors, communicating with clients, managing in-house workers, and preparing invoices. 
                        My final month there, I was put in charge of a couple projects with the offcial tittle as a project manager. 
                        During this time, I decided to look for a different opportunity that hopefully relates to my field of study.
                        As the rule there, I notified their HR office 2 weeks before I left FG. 
                        In the last two weeks, I was able to transfer my in-progress projects to other project managers smoothly without any problem. 
                        Overall, it was a great experience with them.
                    </p>
            </div>    
        </div>
    </>
    );
    
}

 
