import React from 'react'
import {Link} from 'react-router-dom'

function CardItems(props) {
    return (
        <>
            <li className="cards_item">
                <Link className="cards_items_link" to={props.path}>
                    <figure className="cards_item_pic_wrap" data_category={props.label}>
                        <img className="cards_item_img" src={props.src} alt='Explore'/>
                    </figure>
                    <div className="cards_item_info">
                        <h3 className="cards_item_text">{props.text}</h3>
                    </div>
                </Link>
            </li>
        </>
    )
}

export default CardItems
