import React from 'react'
import CardItems from './CardItems'
import './Cards.css'
import CardImage1 from '../images/img-2.jpg'
import CardImage2 from '../images/img-1.jpg'
import CardImage3 from '../images/img-8.jpg'
import { Link } from 'react-router-dom';


function Cards() {
    return (
        <div className="cards">
            <div className="cards_container">
            
                <div className="cards_wrapper">
                    <ul className="cards_items">
                        <CardItems 
                            src={CardImage1}
                            text="New Life in new home land"
                            label='Short-Bio'
                            path='/About'
                        />
                        <CardItems 
                            src={CardImage2}
                            text="Bachelor of Science in Computer Engineering & Minor in Mathematics"
                            label='Education'
                            path='/Education'
                        />
                        <CardItems 
                            src={CardImage3}
                            text="Academic & Work Experience"
                            label='Experience'
                            path='/Experience'
                        />
                    </ul>
                </div>
                <div>
                <Link to='/Contact' >
                    <button className='btn1'>Let's connect</button>
                </Link>
                </div>
            </div>
        </div>
    )
}

export default Cards
