import React from 'react'
import '../../App.css'

export default function Education() {

    return (
        <>
            <h1 className='Education'>Education</h1>
            <div className='Education-body'>
                <div id="edu-content">
                    <h2 id="edu-h">Bachelor of Science in Computer Engineering 
                    <br/>
                    Minor in Mathematics
                    </h2> 
                    <br/>
                    <p id="edu-p">
                        &nbsp;&nbsp;&nbsp;There is always more to learn. 
                        Learning is a process of continuously enhance knowledge. 
                        As long as I thrive to improve myself, I can learn more.
                        This is the reason that I started pursuing a college degree after a long time since I graduated high school.
                        It was hard to start because I didn't know how and where to start.                                                                    
                        After I did some research on where is my best option, I decided to start from a state community college that I can afford.
                        I started at St. Petersburg College (SPC) in Tampa Florida, and I earned my Associate degree in Arts (AA) with Magna Cum Laude there.
                    <br/>                
                    <br/>
                        &nbsp;&nbsp;&nbsp;I transfered to University of Central Florida (UCF, Orlando) to pursue my Bachelor degree after I earned an AA degree at SPC. 
                        At UCF, I decided to work on double majors: Computer Science (CS) and Computer Engineering (CPE).
                        I focused on common courses at the beginning, and then I worked forward CS courses after that.
                        UCF uses an exam called Foundation Exam (FE) to force students who do not pass it with 3 attempts to change their major from CS. 
                        FE is the entrance qualification for my 5 final courses of CS major.                         
                        I didn't make it to earn a CS degree at UCF, so I had to drop it from my study plan there.
                        However, since I majored in CPE which required many math courses, I added a math minor back to my study plan.
                        In August 2020, I graduated from UCF with a Bachelor degree in Computer Engineering with a Minor degree in Mathematics.
                    </p>
                </div>         
            </div>
        </>
    ) 
}

 
