import React from 'react'
import '../../App.css'

export default function About() {

    return (
        <>
        <h1 className='About'>Short-Bio</h1>
        <div className='About-body'>
            <div id="bio-content">
                    <h2 id="bio-h">New life in new home land</h2> 
                    <br/>
                    <p id="bio-p">
                        &nbsp;&nbsp;&nbsp;My family migrated across the world to the US in 2006 right after I graduated high school from Vietnam.
                        My family came to the US with very little to start a new life, so I had to help my parents to settle as soon as possible to keep my younger siblings going to school without delay.                      
                        I couldn't start to go to college at the time, and I needed to start working right away to help support my family.
                        I got a job at a Yatch refinishing company in South Florida for about four years. 
                        It was a fun and well paid job that helped me to support my family during that time.
                        My family settle in a small fishing town in Luissiana, and we have a small fishing business.
                        After my family settled, I went back to school.
                        <br/>                
                        <br/>  
                        &nbsp;&nbsp;&nbsp;I started to go to college since 2010.
                        I didn't have a strong foundation of English to start college classes right away.
                        My first year of going back to school was taking classes of English for academic purppses.            
                        There wasn't a clear plan on what feild of study that I should pick at the begging.
                        I decided to learn about computer technologies because I didn't have any knowledge about computer.
                        Since I enjoy learning new things, I didn't rush through school to get a degree.
                        I took my time with studying to prevent burnt out, so I took time off to work in our family fishing boats at least one semester every year.
                        By doing that, I made enough money to pay for my school expenses without taking loans.
                        It takes longer to finish school, but I am free from debt when I am finished.
                        <br/>                
                        <br/>  
                        &nbsp;&nbsp;&nbsp;Recently, I took an oportunity to work for a construction company for almost a year, and I'd learned so much about many things that would otherwise take me years to learn from school.
                        They put a lot of effort into training me, and I appreciate that.
                        However, I would like to look for other positions that I would fit in better.
                    </p>
            </div>
        </div>
       </>
    );
    
}

 
