import React from 'react'
import '../../App.css'
import { BsTelephoneFill } from "react-icons/bs";
import { BsEnvelopeFill } from "react-icons/bs";
import { BsLinkedin } from "react-icons/bs";




export default function Contact() {
    return (
        <>
            <h1 className='Contact'>Contact</h1>
            <div className='Contact-body'>
                 <div id="cont-content">
                    <div id='phone-div'>
                        <BsTelephoneFill color="royalblue" size="30px"/>
                        <h3 id='phone'>(727) 470 8791</h3>                       
                    </div>
                    <div id='email-div'>
                        <BsEnvelopeFill color="royalblue" size="30px"/>
                        <h3  id='email'>markngn@live.com</h3>
                    </div>
                    <div id='linkedIn-div'>
                        <BsLinkedin color="royalblue" size="30px"/>
                        <a id='linkedIn' href="https://www.linkedin.com/in/mark-nguyen-234b76213/" target="_blank" rel="noreferrer">Let's connect - LinkedIn</a>
                    </div>
                </div>
            </div>
        </>
    )
}